import { Card } from "semantic-ui-react";

const Precio = props => {
  return (
    <Card color="green">
      <Card.Content>
        <Card.Header textAlign="left">Precio del bitcoin: ${}</Card.Header>
      </Card.Content>
      <Card.Content extra>
        <Card.Description />
      </Card.Content>
    </Card>
  );
};

export default Precio;
